<template>
	<div class="attrlist">
		<el-form size="mini">
			<el-form-item
				v-for="(key, index) in styleKeys.filter(item => item != 'rotate')"
				:key="index"
				:label="map[key]"
			>
				<el-color-picker
					v-if="key == 'borderColor'"
					v-model="curComponent.style[key]"
				></el-color-picker>
				<el-color-picker
					v-else-if="key == 'color'"
					v-model="curComponent.style[key]"
				></el-color-picker>
				<el-color-picker
					v-else-if="key == 'backgroundColor'"
					v-model="curComponent.style[key]"
				></el-color-picker>
				<el-select
					v-else-if="key == 'textAlign'"
					v-model="curComponent.style[key]"
				>
					<el-option
						v-for="item in options"
						:key="item.value"
						:label="item.label"
						:value="item.value"
					></el-option>
				</el-select>
				<el-input type="number" v-else v-model="curComponent.style[key]" />
			</el-form-item>
			<el-form-item
				label=""
				v-if="curComponent && !excludes.includes(curComponent.component)"
			>
				<el-input type="textarea" v-model="curComponent.propValue" />
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
export default {
	data() {
		return {
			excludes: ["Picture", "Group"], // 
			options: [
				{
					label: "",
					value: "left"
				},
				{
					label: "",
					value: "center"
				},
				{
					label: "",
					value: "right"
				}
			],
			map: {
				left: "x ",
				top: "y ",
				height: "",
				width: "",
				color: "",
				backgroundColor: "",
				borderWidth: "",
				borderColor: "",
				borderRadius: "",
				fontSize: "",
				fontWeight: "",
				lineHeight: "",
				letterSpacing: "",
				textAlign: "",
				opacity: ""
			}
		};
	},
	computed: {
		styleKeys() {
			return this.$store.state.curComponent
				? Object.keys(this.$store.state.curComponent.style)
				: [];
		},
		curComponent() {
			return this.$store.state.curComponent;
		}
	}
};
</script>

<style lang="less" scoped>
.attrlist {
	overflow: auto;
	padding: 20px;
	padding-top: 0;
	height: 100%;
}
</style>
