// 
export const commonStyle = {
  rotate: 0,
  opacity: 1
};

// 
export const commonAttr = {
  animations: [],
  events: {},
  groupStyle: {}, //  Group 
  isLock: false // 
};

// 
const list = [
  {
    component: "v-text",
    label: "",
    propValue: "",
    icon: "wenben",
    style: {
      width: 200,
      height: 22,
      fontSize: 14,
      fontWeight: 500,
      lineHeight: "",
      letterSpacing: 0,
      textAlign: "",
      color: ""
    }
  },
  {
    component: "v-button",
    label: "",
    propValue: "",
    icon: "button",
    style: {
      width: 100,
      height: 34,
      borderWidth: "",
      borderColor: "",
      borderRadius: "",
      fontSize: 14,
      fontWeight: 500,
      lineHeight: "",
      letterSpacing: 0,
      textAlign: "",
      color: "",
      backgroundColor: ""
    }
  }
];

for (let i = 0, len = list.length; i < len; i++) {
  const item = list[i];
  item.style = { ...commonStyle, ...item.style };
  list[i] = { ...commonAttr, ...item };
}

export default list;
